import { keepPreviousData, useInfiniteQuery } from "@tanstack/react-query"
import { CONTENT_CATEGORIES_INFINITE_PAGE_SIZE } from "../content.constants"
import { getContentCategories } from "../services/getContentCategories"
import { contentCategoriesQueryKeys } from "../utils/contentCategoriesQueryKeys"

function useInfiniteContentCategories() {
  return useInfiniteQuery({
    queryKey: contentCategoriesQueryKeys.paginated(),
    queryFn: params =>
      getContentCategories({
        offset: params.pageParam,
        pageCount: CONTENT_CATEGORIES_INFINITE_PAGE_SIZE,
      }),
    staleTime: 60 * 1000,
    refetchOnWindowFocus: false,
    initialPageParam: 0,
    placeholderData: keepPreviousData,
    getNextPageParam: (lastPage, _allPages, lastPageParam) => {
      if (lastPage.results.length < CONTENT_CATEGORIES_INFINITE_PAGE_SIZE) return undefined
      return lastPageParam + 1
    },
  })
}

export { useInfiniteContentCategories }
